body {
  background: -webkit-gradient(linear, left top, left bottom, from(teal), to(lightblue)) fixed;
  overflow-x: hidden;
}

nav {
  display: flex;
}

.nav-login {
  display: flex;
  list-style: none;
  width: 200px;
  justify-content: space-between;
}

.login {
  color: aliceblue;
  text-transform: uppercase;
  font-weight: bolder;
  cursor: pointer;
  width: 5.8em;
  font-size: 1.1em;
  padding: 5px;
  transition: opacity, transform .5s ease;
  -webkit-transition: opacity, transform .5s ease;
  -moz-transition: opacity, transform .5s ease;
  -ms-transition: opacity, transform .5s ease;
  -o-transition: opacity, transform .5s ease;
}

.login:hover {
  opacity: 0.9;
  text-decoration: underline blanchedalmond;
}

a {
  display: block;
  text-decoration: none;
}

.active {
  font-weight: bolder;
  font-size: 1.1em;
  transition: font-size 0.4s ease-in;
  -webkit-transition: font-size 0.4s ease-in;
  -moz-transition: font-size 0.4s ease-in;
  -ms-transition: font-size 0.4s ease-in;
  -o-transition: font-size 0.4s ease-in;
}

.active-route {
  position: absolute;
  background-color: rgb(5, 255, 88);
  box-shadow: 0 0 3px 1px rgb(5, 255, 88);
  bottom: 0;
  width: 100%;
  height: 3px;
  transition: all 0.6s ease-in-out;
  -webkit-transition: all 0.6s ease-in-out;
  -moz-transition: all 0.6s ease-in-out;
  -ms-transition: all 0.6s ease-in-out;
  -o-transition: all 0.6s ease-in-out;
}

.user-profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 30px;
  right: 50px;
  width: 4rem;
  height: 6.3em;
  background-image: linear-gradient(grey, lightgrey);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.user-profile:hover>.user-avatar {
  animation: profile-animate 1.2s ease infinite;
  -webkit-animation: profile-animate 1.2s ease infinite;
}

.user-avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  background-color: grey;
}

.fa-user-tie {
  font-size: 1.7em
}

.user-name {
  font-size: 1em;
  font-weight: 600;
  height: 30px;
}

.log-out {
  width: 100%;
  height: 30px;
  background-color: transparent;
  font-size: 12px;
  border: none;
  border-top: 1px solid black;
  margin-top: 5px;
  cursor: pointer;
  outline: none;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  transition: background-color 0.3s ease-out;
  -webkit-transition: background-color 0.3s ease-out;
  -moz-transition: background-color 0.3s ease-out;
  -ms-transition: background-color 0.3s ease-out;
  -o-transition: background-color 0.3s ease-out;
}

.log-out:hover {
  background-color: rgb(230, 217, 217);
}

.headers {
  -webkit-text-stroke: 3px black;
  color: transparent;
  font-size: 3.2em;
  letter-spacing: 1px;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  text-align: center;
  font-size: 38px;
  margin-top: 100px;
}

.username-section,
.password-section,
.second-name-section,
.phone,
.email-section,
.planet,
.login-email-section,
.login-password-section {
  position: relative;
  margin-bottom: 20px;
}

section {
  position: relative;
}

.phone-section {
  display: flex;
  width: 284px;
  justify-content: left;
}

.inputs-style {
  width: 270px;
  height: 45px;
  margin-bottom: 10px;
  box-sizing: border-box;
  outline: none;
  cursor: pointer;
  font-size: 15px;
  border-radius: 0.4em;
  transition: box-shadow, border-radius, transform .3s ease;
  -webkit-transition: box-shadow, border-radius, transform .3s ease;
  -moz-transition: box-shadow, border-radius, transform .3s ease;
  -ms-transition: box-shadow, border-radius, transform .3s ease;
  -o-transition: box-shadow, border-radius, transform .3s ease;
  -webkit-border-radius: 0.4em;
  -moz-border-radius: 0.4em;
  -ms-border-radius: 0.4em;
  -o-border-radius: 0.4em;
}

.inputs-style:hover,
.inputs-style:focus {
  box-shadow: 0px 0px 10px 5px rgb(16, 227, 255);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  transform: scale(1.05);
  -webkit-transform: scale(1.05);
  -moz-transform: scale(1.05);
  -ms-transform: scale(1.05);
  -o-transform: scale(1.05);
}

.inputs-icon {
  position: absolute;
  right: -3px;
  top: -1px;
  width: 41px;
  height: calc(100% - 27px);
  background-color: rgb(0, 174, 180);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.3em;
  -webkit-border-radius: 0.3em;
  -moz-border-radius: 0.3em;
  -ms-border-radius: 0.3em;
  -o-border-radius: 0.3em;
  pointer-events: none;
  transition: transform .6s ease;
  -webkit-transition: transform .6s ease;
  -moz-transition: transform .6s ease;
  -ms-transition: transform .6s ease;
  -o-transition: transform .6s ease;
}

input:hover~.inputs-icon,
input:focus~.inputs-icon {
  transform: scaleX(1.44)scaleY(1.2);
  -webkit-transform: scaleX(1.44)scaleY(1.2);
  -moz-transform: scaleX(1.44)scaleY(1.2);
  -ms-transform: scaleX(1.44)scaleY(1.2);
  -o-transform: scaleX(1.44)scaleY(1.2);
}

input:hover~.inputs-icon.phone-icon {
  transform: scaleX(1.3)scaleY(1.1);
  -webkit-transform: scaleX(1.3)scaleY(1.1);
  -moz-transform: scaleX(1.3)scaleY(1.1);
  -ms-transform: scaleX(1.3)scaleY(1.1);
  -o-transform: scaleX(1.3)scaleY(1.1);
}

.labels-style {
  position: absolute;
  left: 5px;
  top: 0;
  transition: all .5s ease-in-out;
  font-size: 16px;
  font-weight: 500;
  -webkit-transition: all .5s ease-in-out;
  -moz-transition: all .5s ease-in-out;
  -ms-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  cursor: pointer;
  pointer-events: none;
  opacity: 0.8;
}

.phone-icon {
  height: calc(100% - 25px);
}

.phone-code {
  position: relative;
}

#code-phone {
  width: 70px;
  height: 43px;
  border-radius: 10px;
  text-align: center;
  font-size: 18px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  margin-right: 10px;
  margin-left: 5px;
  outline: none;
  cursor: pointer;
  -webkit-appearance: none;
}

.code-phone-arrow {
  position: absolute;
  right: 13px;
  top: 10%;
  z-index: 1;
  color: #111111;
  pointer-events: none;
  transition: transform .4s ease-in-out;
  -webkit-transition: transform .4s ease-in-out;
  -moz-transition: transform .4s ease-in-out;
  -ms-transition: transform .4s ease-in-out;
  -o-transition: transform .4s ease-in-out;
}

#code-phone:focus+.fa-arrow-down {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
}

#phone-number {
  width: 190px;
  height: 45px;
  outline: none;
  cursor: pointer;
  font-size: 15px;
  transition: box-shadow, border-radius, transform .3s ease;
  -webkit-transition: box-shadow, border-radius, transform .3s ease;
  -moz-transition: box-shadow, border-radius, transform .3s ease;
  -ms-transition: box-shadow, border-radius, transform .3s ease;
  -o-transition: box-shadow, border-radius, transform .3s ease;
}

.label-onclick-size {
  font-size: 12px;
  opacity: 1;
  color: rgb(29, 248, 0);
}

.submit-button:hover {
  color: white;
}

.submit-button:disabled {
  background-color: rgba(180, 248, 248, 0.719);
}

option {
  color: white;
  background-color: rgb(0, 113, 128);
}

.gender {
  display: flex;
  justify-content: space-around;
  width: 275px;
  position: relative;
  right: 10px;
}

.male,
.female {
  width: 50px;
  height: 50px;
  position: relative;
  display: block;
  cursor: pointer;
}

.fa-male,
.fa-female {
  font-size: 24px;
  color: white;
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-10%, -35%);
  -webkit-transform: translate(-10%, -35%);
  -moz-transform: translate(-10%, -35%);
  -ms-transform: translate(-10%, -35%);
  -o-transform: translate(-10%, -35%);
}

#female {
  -webkit-appearance: none;
  width: 50px;
  height: 50px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  border: 1px solid white;
  background-color: violet;
  transition: box-shadow, background-color, transform .6s ease;
  -webkit-transition: box-shadow, background-color, transform .6s ease;
  -moz-transition: box-shadow, background-color, transform .6s ease;
  -ms-transition: box-shadow, background-color, transform .6s ease;
  -o-transition: box-shadow, background-color, transform .6s ease;
  outline: none;
  cursor: pointer;
}

#female:hover {
  background-color: rgb(238, 183, 238);
}

#male {
  -webkit-appearance: none;
  width: 50px;
  height: 50px;
  background-color: rgb(37, 162, 194);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  border: 1px solid white;
  transition: box-shadow, background-color, transform .6s ease;
  -webkit-transition: box-shadow, background-color, transform .6s ease;
  -moz-transition: box-shadow, background-color, transform .6s ease;
  -ms-transition: box-shadow, background-color, transform .6s ease;
  -o-transition: box-shadow, background-color, transform .6s ease;
  outline: none;
  cursor: pointer;
}

#male:hover {
  background-color: rgb(105, 201, 224);
}

#female:checked {
  background-color: rgb(206, 12, 206);
  box-shadow: 0 0 3px 5px violet;
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}

#male:checked {
  background-color: rgb(12, 121, 151);
  box-shadow: 0 0 3px 5px rgb(45, 164, 197);
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}

.has-error {
  border: 1px solid red;
  box-shadow: 0 0 1px 1px red;
}

.valid {
  color: greenyellow;
  font-size: 16px;
  height: 20px;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -moz-transition: all 0.4s;
  -ms-transition: all 0.4s;
  -o-transition: all 0.4s;
}

.error {
  color: red;
  position: relative;
  height: 20px;
  font-size: 14px;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -moz-transition: all 0.4s;
  -ms-transition: all 0.4s;
  -o-transition: all 0.4s;
}

.sex-err {
  margin-top: 25px;
}

.planet {
  -webkit-appearance: none;
  padding: 0 0 0 10px;
  position: relative;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.arrow-select {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: -1px;
  width: 41px;
  height: calc(100% - 10px);
  background-color: rgb(0, 150, 180);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  pointer-events: none;
  transition: transform .3s ease-in-out;
  -webkit-transition: transform .3s ease-in-out;
  -moz-transition: transform .3s ease-in-out;
  -ms-transition: transform .3s ease-in-out;
  -o-transition: transform .3s ease-in-out;
  box-shadow: 0 0 1px 1px #111111;
}

.inputs-style:hover+.arrow-select,
.inputs-style:focus+.arrow-select {
  transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -ms-transform: scale(1.2);
  -o-transform: scale(1.2);
}

.inputs-style:focus+.arrow-select>.fa-arrow-up {
  font-size: 23px;
}

.inputs-style:focus+.arrow-select>.fa-arrow-down {
  font-size: 13px;
}

option:checked {
  background-color: rgb(37, 255, 255);
}

.fa-arrow-up {
  color: bisque;
  font-size: 15px;
  transition: font-size .3s ease-in-out;
  -webkit-transition: font-size .3s ease-in-out;
  -moz-transition: font-size .3s ease-in-out;
  -ms-transition: font-size .3s ease-in-out;
  -o-transition: font-size .3s ease-in-out;
}

.fa-arrow-down {
  color: #111111;
  font-size: 23px;
  transition: all .3s ease-in-out;
  -webkit-transition: all .3s ease-in-out;
  -moz-transition: all .3s ease-in-out;
  -ms-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
}

.submit-button {
  outline: none;
  border: 1px solid rgb(16, 227, 255);
  color: rgb(16, 227, 255);
  border-radius: 10px;
  background-color: white;
  width: 170px;
  cursor: pointer;
  height: 100px;
  position: relative;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  transition: color .5s ease-in-out;
  -webkit-transition: color .5s ease-in-out;
  -moz-transition: color .5s ease-in-out;
  -ms-transition: color .5s ease-in-out;
  -o-transition: color .5s ease-in-out;
  margin-top: 40px;
  margin-bottom: 10px;
  font-size: 24px;
}

.submit-button::before {
  content: "";
  border-radius: 50%;
  width: 0;
  height: 0;
  position: absolute;
  background-color: rgb(16, 227, 255);
  top: 0;
  left: 0;
  transition: width, height, border-radius .5s ease-in-out;
  -webkit-transition: width, height, border-radius .5s ease-in-out;
  -moz-transition: width, height, border-radius .5s ease-in-out;
  -ms-transition: width, height, border-radius .5s ease-in-out;
  -o-transition: width, height, border-radius .5s ease-in-out;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}

.submit-button:active {
  box-shadow: 0 0 5px 5px rgb(16, 227, 255);
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}

.submit-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}

.submit-button:hover::before {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.result {
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
}

/*table with users*/
table {
  margin-top: 20px;
  border-collapse: collapse;
  margin-bottom: 15px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
}

th {
  border: 1px solid black;
  padding: 5px;
}

td {
  border: 1px solid black;
  padding: 10px;
}

.delete-user-btn {
  background-color: rgb(0, 248, 165);
  border: 2px solid rgb(219, 192, 152);
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  width: 100px;
  height: 30px;
  color: rgb(58, 54, 54);
  cursor: pointer;
  outline: none;
  font-size: 1.2em;
  font-weight: bold;
  transition: background-color, box-shadow, transform 0.3s ease;
  -webkit-transition: background-color, box-shadow, transform 0.3s ease;
  -moz-transition: background-color, box-shadow, transform 0.3s ease;
  -ms-transition: background-color, box-shadow, transform 0.3s ease;
  -o-transition: background-color, box-shadow, transform 0.3s ease;
}

.delete-user-btn:hover {
  background-color: rgb(16, 172, 120);
  box-shadow: 2px 1px 3px 2px rgb(219, 192, 152);
  transform: scale(1.05);
  -webkit-transform: scale(1.05);
  -moz-transform: scale(1.05);
  -ms-transform: scale(1.05);
  -o-transform: scale(1.05);
}

/*sing in router*/

.sing-in-componets {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.login-input {
  margin-bottom: 20px;
  width: 250px;
  height: 25px;
  outline: none;
}

@keyframes profile-animate {
  0% {
    transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
  }
}

@media only screen and (max-width:1000px) {
  nav {
    width: 290px;
    margin-right: 30px;
  }

  .nav-login a {
    font-size: 0.9em;
  }

  .nav-login {
    display: block;
  }

  .user-profile {
    top: 20px;
    right: 5px;
  }

  .table-section {
    overflow-x: auto;

  }

  table {
    display: block;
  }

  th,
  td {
    padding: 2px;
    font-size: 0.75em;
    text-align: center;
    width: 50px;
  }

  .delete-user-btn {
    width: 60px;
    margin: 3px;
    font-size: 1em;
  }
}